<template>
  <div id="team-rank">
    <head-top title="小组成员"></head-top>
    <div class="main">
      <div class="item" v-for="(item, k) in dataList" :key="k">
        <div class="pm">
          <img v-if="item['Ranking']===1 && k<3 " src="../../../assets/icon_jp_normal.png" alt=""/>
          <img v-else-if="item['Ranking']===2 && k<3 " src="../../../assets/icon_yp_normal.png" alt=""/>
          <img v-else-if="item['Ranking']===3 && k<3 "
               src="../../../assets/icon_tongp_normal.png" alt=""/>
          <span v-else>{{item['Ranking']}}</span>
        </div>
        <div class="toux">
          <img :src="defaultHead" v-real-img="[ item['UserID'], item['StudentID'] ]" alt/>
        </div>
        <div class="info">
          <div class="name-sf">
            <span class="name">{{item['StudentName']}}</span>
            <span
              :class="item['IsGroupLeader'] ?'member leader':'member normal'">{{ item['IsGroupLeader'] ?'组长':'成员' }}</span>
          </div>
          <div class="defen-huozan">
            <div class="defen">
              得分
              <span>{{item['TotalScore']||0}}分</span>
            </div>
            <div class="huozan">
              获赞
              <span>{{item['TotalGood']||0}}个</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data () {
    return {
      defaultHead: require("./../../../assets/default_head.png"),
      dataList: []
    };
  },
  created () {
    this.getDataInfo();
  },
  methods: {
    getDataInfo () {
      this.dataList = [];

      let reqData = {
        groupID: this.$route.params.groupId,
        gsid: this.$route.params.id
      };
      this.$mainHttp.group.getDiscussRankList(reqData).then(res => {
        if (res.Result && res.Datas && res.Datas.length > 0) {
          res.Datas.forEach(item => {
            this.dataList.push(item);
          });
        }
      }).catch(err => {
      });
    }
  }
};
</script>

<style lang='less' scoped>
@import url("./../../../styles/common/base.less");

#team-rank {
  width: 100%;
  height: 100%;
  background: #fff;

  .main {
    .item {
      padding-top: 5.3vw;
      padding-bottom: 5.3vw;
      height: 10.7vw;
      width: 92vw;
      border-bottom: 1px solid #dddddd;
      text-align: left;
      overflow: hidden;
      margin: 0 auto;

      div {
        display: inline-block;
      }

      .pm {
        // background-image: url("../../../assets/icon_jp_normal.png");
        width: 22px;
        height: 34px;
        margin-right: 17px;
        background-size: cover;

        img {
          max-width: 22px;
          max-height: 34px;
        }

        span {
          font-size: @font-title * 1.6;
          font-weight: bolder;
          color: #888;
          text-align: center;
          margin-left: 5px;
        }
      }

      @imgWidth: 10.7vw;

      .toux {
        width: @imgWidth;
        height: @imgWidth;
        margin-right: 4vw;

        img {
          margin: 0 auto;
          max-width: @imgWidth;
          max-height: @imgWidth;
        }
      }

      .info {
        width: 40%;

        .name-sf {
          span {
            display: inline-block;
          }

          .name {
            font-size: @font-sub-title;
            font-family: MicrosoftYaHei;
            color: rgba(51, 51, 51, 1);
            margin-right: 2.7vw;
            margin-bottom: 2.7vw;
          }

          .member {
            width: 8.5vw;
            height: 4vw;
            // background: rgba(247, 163, 65, 1);
            border-radius: 3px;
            font-size: @font-info;
            color: #fff;
            text-align: center;
          }

          .leader {
            background: rgba(247, 163, 65, 1);
          }

          .normal {
            background: #00cbab;
          }
        }

        .defen-huozan {
          font-size: @font-info;
          font-family: MicrosoftYaHei;
          display: flex;
          color: #666;
          justify-content: space-between;

          span {
            margin-left: 2.7vw;
            color: #00cbab;
          }
        }
      }
    }

    .footer {
      margin: 2vh 0;

      .van-button {
        width: 200px;
        background: #00cbab;
        color: #fff;
      }
    }
  }
}
</style>



